*{
  padding:0px;
  margin: 0px;
  box-sizing: border-box;
 
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
body{text-align: center;}
.heading{
  padding:20px;
  font-size:52px;
  font-weight: 100;
  text-align: center;
  background-color: white;
  border-bottom: 1px solid #495464;
}
.sidebar{
  font-size:20px;
  font-weight: 100;
  width:30%;
}
.sidebar ul{
  padding:30px;
}
.sidebar ul li{
  list-style: none;
  padding: 20px;
  background-color: #E8E8E8;
  border-bottom: 1px solid white;
  border-radius:20px;
  margin:10px 0px;
}
.content{
  display: flex;

}
.on_btn{
  border-radius: 10px;
  background-color: red;
  padding:10px;
  position: relative;
  width:50px;
  height:25px;
  border: none;
}
.off_btn{
  border-radius: 10px;
  background-color: rgb(23, 85, 24);
  padding:10px;
  position: relative;
  width:50px;
  height:25px;
  border: none;
}
.on_btn .circle{
  position: absolute;
  border-radius: 40%;
  width:40%;
  height:90%;
  top:1px;
  left:4px;
  background-color: aliceblue;
}
.off_btn .circle{
  position: absolute;
  border-radius: 40%;
  width:40%;
  height:90%;
  top:1px;
  right:4px;
  background-color: aliceblue;
}

.maincontent{
  width:80%;
}
.maincondiv{
  width:100%
}
svg{
  width:30px;
  height:30px;
  border:1px solid black
}


.PostPrints{
  padding:30px;
  width:80%;
  background-color: #E8E8E8;
  border-radius: 10px;
  margin-top: 20px;

}
.PostPrints input{
margin-bottom:20px;
margin: 10px;
width:100px;
border-radius: 10px;


}
.PostPrints label{
  margin-right: 30px;
}
.Visible{
  display: flex;
  justify-content:space-around;
  align-items: center;
  padding:10px;
  background-color: #E8E8E8;
  margin:5px 0px;
  border-radius: 10px;
}
.Visible h1,h3,.collapse h1,h3,h4,h2{
  font-weight:100;
}
.collapse{
  display: none;
  display: flex;
  width:100%;
  justify-content:center;
  align-items: center;
  background-color: aquamarine;
}
.collapse2{display: none}
.collapse img{
  height:70%;
}
.maincondiv img{
  width:50%;
  height:100%;
}
@media (max-width:768px){
  .sidebar{
    width:100%;
    text-align: center;
    
  }
  .sidebar ul li{
    display: inline;
    margin: 20px;
    background: none;
  }
  .content{
    display: flex;
    flex-direction: column;
    margin:0px 10px;
  }
  .maincontent{
    width:100%;
   
  }
  .collapse{
    flex-direction: column;
    
  }
}